.header-Bar {
  height: 80px;
}
.callMenuDesktop a {
  text-decoration: none;
}
.callMenuDesktop a span {
  font-size: 14px;
  color: #00aaee !important;
}
.callMenuDesktop a:hover {
  filter: grayscale(0.2);
}
.logoHeader {
  width: 100%;
  max-width: 250px;
}
.navbar-color-grey {
  height: 50px;
  background: #efefef;
  line-height: 33px;
  border-bottom: 1px solid #acacac;
}
.navbar-color-grey ul {
  margin: 0 auto;
  padding: 0;
}
.navbar-color-grey ul li {
  display: inline;
  text-transform: uppercase;
  font-size: 10px;
  font-weight: bold;
}
@media (min-width: 992px) {
  .navbar-color-grey ul li {
    font-size: 12px;
  }
}
.navbar-color-grey ul li a {
  color: black;
  text-decoration: none;
}
.navbar-color-grey ul li a.active {
  color: #00aaee !important;
}
.navbar-color-grey ul li a:hover {
  color: #00aaee !important;
}
.windowCloseClick {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.MegaMenu-top-one {
  top: 130px;
  position: absolute;
}
.MegaMenu-top-two {
  top: 50px;
  position: fixed;
}
.MegaMenu {
  left: 0;
  right: 0;
  bottom: -80px;
  background: #E3E3E3;
}
.MegaMenu .row.megaMenuRow {
  background: #efefef;
}
.MegaMenu .row .SectionsInMegaMenu {
  margin: 5px 0 0;
  padding: 5px 10px;
  border-bottom: 2px solid black;
  cursor: pointer;
}
.MegaMenu .row .SectionsInMegaMenu.active {
  color: #00aeef !important;
  border-bottom-color: #00aeef !important;
}
.MegaMenu .row .SectionsInMegaMenu:hover {
  color: #00aeef;
  border-bottom-color: #00aeef;
  transition: 0.1s;
}
.MegaMenu .row .SectionsInMegaMenu:not(:hover) {
  color: black;
  border-bottom-color: black;
  transition: 0.1s;
}
.MegaMenu .row .pagesViewMegaMenu {
  margin: 15px 5px;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(167px, 1fr));
  grid-gap: 10px;
}
.MegaMenu .row .pagesViewMegaMenu a {
  color: black;
}
.MegaMenu .row .pagesViewMegaMenu a:hover {
  text-decoration: none;
  color: #00aeef !important;
}
.MegaMenu .row .pagesViewMegaMenu .insertPagesOnMegamenu img {
  height: auto;
}
.MegaMenu .row .pagesViewMegaMenu .insertPagesOnMegamenu p {
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  margin-top: 5px;
}
.padding-top-withFixedMenu {
  height: 50px;
}
.gamburgerMenu {
  height: 30px;
}
.MobilMenu {
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  background: #efefef;
  transition: 0.3s;
  z-index: 100;
}
.transformMobilMenu {
  margin-left: -100%;
}
.sectionsAndPages {
  width: 100%;
  height: 100%;
  padding-top: 65px;
  background: #E3E3E3;
}
.sectionPagesMobilMenu {
  top: 65px;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  position: absolute;
  background: #efefef;
  transition: 0.3s;
  border-top: 1px solid #acacac;
  overflow-y: scroll;
}
.clickPointer span {
  cursor: pointer;
}
.clickPointer span:hover {
  color: #00aaee;
}
.fontText {
  font-family: 'Montserrat', sans-serif !important;
}
