.margin-padding-0 {
  margin: 0;
  padding: 0;
}
footer .row-searchAndSubscribe {
  background: #00aeef;
  min-height: 60px;
}
.FooterVar {
  width: 100%;
  margin: 10px 0;
  height: 40px;
  text-align: center;
  padding: 0 5px;
}
.searchFooter input {
  width: 100%;
  margin: 10px 0;
  height: 40px;
  text-align: center;
  padding: 0 5px;
  border: none;
}
.socialFooter input {
  width: 100%;
  margin: 10px 0;
  height: 40px;
  text-align: center;
  padding: 0 5px;
  border: 2px solid white;
  background: #008cbf;
  color: white;
}
.row-listSectionAndPages {
  background: #a1a1a1;
}
.listContainer {
  padding: 15px 0;
  height: 50px;
  -moz-column-width: 100px;
  column-width: 100px;
  -moz-column-gap: 0;
  column-gap: 0;
  -moz-column-fill: auto;
  column-fill: auto;
}
@media (min-width: 768px) {
  .listContainer {
    height: 560px;
    -moz-column-width: 170px;
    column-width: 170px;
  }
}
@media (min-width: 992px) {
  .listContainer {
    height: 450px;
  }
}
@media (min-width: 1200px) {
  .listContainer {
    height: 380px;
  }
}
.footerListSections {
  margin: 0;
  padding: 0;
}
.footerListSections li {
  list-style-type: none;
  font-size: 12px;
  font-weight: bold;
}
.footerListSections li .footerListPages {
  margin: 0;
  padding: 0;
  padding: 0 10px 0 0!important ;
  margin-bottom: 10px;
}
.footerListSections li .footerListPages li {
  font-weight: normal;
}
.footerListSections li .footerListPages li a {
  color: white;
}
.footerListSections li .footerListPages li span.redColor {
  color: #FF0000;
  margin-left: 4px;
}
.footerInfo {
  background: #a1a1a1;
}
.footerInfo .callFooter a {
  color: #212529;
  font-size: 12px;
}
.footerInfo .callFooter a span.colorWhite {
  color: white;
}
.footerInfo .footerAddress {
  color: white;
  font-size: 12px;
}
.footerInfo .footerAddress .colorBlack {
  color: #212529;
}
